import { useRef, useState, useEffect } from "react";
import * as htmlToImage from "html-to-image";
import download from "downloadjs";
import { Card, Button } from "./components/1/ui";
import qr from "./img/qr.png";
const ReceiptGenerator = ({ data, onReset }) => {
  const receiptRef = useRef(null);
  const [sortedDates, setSortedDates] = useState([]);
  const [totalSessions, setTotalSessions] = useState(0);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");

  // Hm nhn d liu t ReceiptForm
  useEffect(() => {
    if (data && data.attendanceDates.length > 0) {
      // Chuyn chui ngy sang i tng Date, km gi li chui gc
      const datesWithObj = data.attendanceDates.map((dateStr) => {
        const [day, month, year] = dateStr.split("/");
        return {
          original: dateStr,
          dateObj: new Date(year, month - 1, day),
        };
      });

      // Sp xp theo thi gian
      const sortedDatesArray = datesWithObj
        .sort((a, b) => a.dateObj - b.dateObj)
        .map((item) => item.original);

      setSortedDates(sortedDatesArray);
      setTotalSessions(sortedDatesArray.length);
      setStartDate(sortedDatesArray[0]);
      setEndDate(sortedDatesArray[sortedDatesArray.length - 1]);
    }
  }, [data]);

  const downloadReceipt = () => {
    if (!receiptRef.current || !data) {
      alert(" Vui lng nhp thng tin bin lai trc khi ti xung!");
      return;
    }

    const firstDate = new Date(startDate.split("/").reverse().join("-"));
    const monthYear = `${firstDate.getMonth() + 1}-${firstDate.getFullYear()}`;
    const fileName = `${data.studentName}_Thu_hc_ph_thng_${monthYear}.png`;

    setTimeout(() => {
      htmlToImage
        .toPng(receiptRef.current)
        .then((dataUrl) => {
          download(dataUrl, fileName);
        })
        .catch((error) => {
          console.error(" Li khi to nh:", error);
          alert("Khng th to nh ha n. Vui lng th li!");
        });
    }, 500);
  };

  return (
    <div>
      <Card>
        <div
          ref={receiptRef}
          style={{
            textAlign: "left",
            padding: "20px",
            paddingRight: "20px",
            backgroundColor: "white",
            width: "525px",
            border: "1px solid #ccc",
          }}
        >
          <h2
            style={{
              textAlign: "center",
              textTransform: "uppercase",
              fontSize: "1.5rem",
            }}
          >
            BIN LAI THU TIN
          </h2>
          <p>
            <strong>Hc sinh:</strong> {data.studentName}
          </p>
          <p>
            <strong>Lp:</strong> {data.className}
          </p>
          <p>
            <strong>S tin:</strong> {data.amount} 
          </p>
          <p>
            <strong>Ni dung:</strong> Thu hc ph {totalSessions} bui t{" "}
            {startDate} n {endDate}
          </p>

          <h3 style={{ marginTop: "10px" }}>Bng im danh</h3>
          <table style={{ width: "100%", borderCollapse: "collapse" }}>
            <thead>
              <tr style={{ backgroundColor: "#f5f5f5", textAlign: "left" }}>
                <th style={{ padding: "8px", borderBottom: "1px solid #ddd" }}>
                  Ngy
                </th>
                <th style={{ padding: "8px", borderBottom: "1px solid #ddd" }}>
                  Trng thi
                </th>
              </tr>
            </thead>
            <tbody>
              {sortedDates.map((date, index) => (
                <tr key={index} style={{ borderBottom: "1px solid #ddd" }}>
                  <td style={{ padding: "8px" }}>{date}</td>
                  <td style={{ padding: "8px" }}>C hc</td>
                </tr>
              ))}
            </tbody>
          </table>

          <br />
          <div
            style={{ textAlign: "center", marginTop: "20px", margin: "0 auto" }}
          >
            <h4>Qut m  thanh ton</h4>
            <img
              src={qr}
              alt="QR Code"
              style={{ width: "100px", height: "100px" }}
            />
            <p>Vietcombank</p>
            <p>S ti khon: 1020102766</p>
            <p>Ch ti khon: Nguyen Hue Thien</p>
          </div>
        </div>

        <Button onClick={downloadReceipt}>Ti Bin Lai</Button>
        <Button onClick={onReset}>To Bin Lai Mi</Button>
      </Card>
    </div>
  );
};

export default ReceiptGenerator;
