import React, { useState } from "react";
import { HashRouter as Router, Route, Routes } from "react-router-dom";
import ReceiptForm from "./ReceiptForm";
import ReceiptGenerator from "./ReceiptGenerator";

function App() {
  const [receiptData, setReceiptData] = useState(null);

  const handleGenerate = (data) => {
    setReceiptData(data); // Cp nht d liu bin lai
  };

  const handleReset = () => {
    setReceiptData(null); // Reset d liu bin lai, form vn hin th
  };

  return (
    <Router>
      <div style={{ maxWidth: "600px", margin: "auto", padding: "20px" }}>
        {/* Form lun hin th */}
        <ReceiptForm onGenerate={handleGenerate} />

        {/* iu hng v hin th bin lai nu c d liu */}
        <Routes>
          <Route
            path="/"
            element={
              receiptData ? (
                <ReceiptGenerator data={receiptData} onReset={handleReset} />
              ) : (
                <div>Vui lng to bin lai.</div>
              )
            }
          />
          {/* Thm cc route khc nu cn */}
        </Routes>
      </div>
    </Router>
  );
}

export default App;
