import { useState } from "react";
import { Input, Button, Card } from "./components/1/ui";
import "./formStyle.css";

const ReceiptForm = ({ onGenerate }) => {
  const [studentName, setStudentName] = useState("");
  const [className, setClassName] = useState("");
  const [amount, setAmount] = useState(500000);
  const [dates, setDates] = useState("");

  const handleSubmit = () => {
    if (!studentName || !className || !amount || !dates) {
      alert(" Vui lng nhp y  thng tin!");
      return;
    }

    const currentYear = new Date().getFullYear(); // Ly nm hin ti
    const attendanceDates = dates
      .split(" ") // Tch ngy theo du cch
      .map((date) => {
        let parts = date.trim().split("/");
        if (parts.length !== 2) {
          alert(
            ` Sai nh dng ngy: ${date}. Vui lng nhp theo nh dng d/m`
          );
          return null;
        }

        let [day, month] = parts.map(Number);
        if (month > 12 || month < 1 || day < 1 || day > 31) {
          alert(` Ngy khng hp l: ${date}`);
          return null;
        }

        return `${day}/${month}/${currentYear}`;
      })
      .filter(Boolean);

    const receipt = {
      studentName,
      className,
      amount,
      attendanceDates,
    };

    console.log(" D liu bin lai gi i:", receipt);
    onGenerate(receipt); // Gi d liu bin lai ln parent component
  };

  return (
    <Card>
      <h2>Nhp thng tin hc ph</h2>
      <Input
        label="H tn hc sinh:"
        value={studentName}
        onChange={(e) => setStudentName(e.target.value)}
      />
      <Input
        label="Lp:"
        value={className}
        onChange={(e) => setClassName(e.target.value)}
      />
      <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
        <Input
          label="S tin (VN):"
          type="number"
          value={amount}
          onChange={(e) => setAmount(Number(e.target.value))}
        />
        <Button
          className="small-button"
          onClick={() => setAmount(amount + 100000)}
        >
          +100K
        </Button>
        <Button
          className="small-button"
          onClick={() => setAmount(Math.max(100000, amount - 100000))}
        >
          -100K
        </Button>
      </div>
      <Input
        className="wide-input" // Thm class cho  nhp ngy
        label="Ngy hc (VD: 2/1 3/1 10/1 12/1):"
        value={dates}
        onChange={(e) => setDates(e.target.value)}
      />
      <Button onClick={handleSubmit}>To Bin Lai</Button>
    </Card>
  );
};

export default ReceiptForm;
